import React, { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";
import logoBlanco from "../../images/logoBlanco.svg";
import logoSercotec from "../../images/logoSercotec.png";
import "./Footer.css";
import "./FooterResponsive.css";

function Footer() {
  const [year, setYear] = useState(0);

  const thisYear = () => {
    const date = new Date();
    return date.getFullYear();
  };

  useEffect(() => {
    setYear(thisYear());
  }, []);

  return (
    <footer>
      <div className="footerContainer">
        <div className="footerLogo">
          <img src={logoBlanco} alt="" />
        </div>

        <div className="footerLinksWrapper">
          <div className="footerLinksContainer">
            <a href="#nosotros" className="footerLink">
              Nuestra historia
            </a>
          </div>

          <div className="footerLinksContainer">
            <a href="#servicios" className="footerLink">
              Servicios funerarios
            </a>
          </div>

          <div className="footerLinksContainer">
            <a href="#contacto" className="footerLink">
              Contacto
            </a>
          </div>

          <div className="footerLinksContainer">
            <a href="#faqs" className="footerLink">
              Preguntas frecuentes
            </a>
          </div>

          <div className="footerLinksContainer">
            <NavLink to="/obituario" className="footerLink">
              Obituario
            </NavLink>
          </div>

          <div className="footerLinksContainer linkRRSS">
            <a
              href="https://web.facebook.com/funerariasangol"
              className="footerLink"
            >
              <svg
                xmlns="https://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                className="bi bi-facebook"
                viewBox="0 0 16 16"
              >
                <path d="M16 8.049c0-4.446-3.582-8.05-8-8.05C3.58 0-.002 3.603-.002 8.05c0 4.017 2.926 7.347 6.75 7.951v-5.625h-2.03V8.05H6.75V6.275c0-2.017 1.195-3.131 3.022-3.131.876 0 1.791.157 1.791.157v1.98h-1.009c-.993 0-1.303.621-1.303 1.258v1.51h2.218l-.354 2.326H9.25V16c3.824-.604 6.75-3.934 6.75-7.951z" />
              </svg>
            </a>
          </div>
        </div>
      </div>

      <div className="selloSercotec">
        <img src={logoSercotec} alt="" />
      </div>
      <div className="footerCopyright">
        <p>© {year} Funeraria Cristo Rey - Angol</p>
      </div>
    </footer>
  );
}

export default Footer;
